<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-order" id="order-list">
        <el-tabs type="border-card" style="height:100%">
          <el-tab-pane label="点餐">
            <el-table :data="tableData">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="price" label="金额"></el-table-column>
              <el-table-column prop="count" label="数量">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button type="text" icon="el-icon-edit" v-on:click="addOrderList(scope.row)" size="mini">增加</el-button>
                    <el-button type="text" icon="el-icon-delete" v-on:click="delSingleGoods(scope.row)"  size="mini">删除</el-button>
                </template>
              </el-table-column>
            </el-table> 
            <el-row type="flex" class="row-bg" justify="center" style="margin-top:10px;">
              <!-- <el-input-number size="mini" v-model="num1" label="描述文字"></el-input-number> -->
              <el-col :span="6"><div class="grid-content bg-purple">總數量: {{totalCount}}</div></el-col>
              <el-col :span="6"><div class="grid-content bg-purple-light">金額: <span class="o-price">￥{{totalMoney}} </span></div></el-col>
              
            </el-row>
            <el-row type="flex" class="buttonGroup" justify="end">
              <el-button-group>
                <el-button type="">挂单</el-button>
                <el-button type="" v-on:click="delAllGoods()">清空</el-button>
                <el-button type="primary" v-on:click="checkout()">结账</el-button>
              </el-button-group>  
            </el-row>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            挂单
          </el-tab-pane>
          <el-tab-pane label="外卖">
            外卖
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span='17'>
        <div class="often-goods">
          <h4 class="title"><i class="el-icon-star-off"></i> 常点商品</h4>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in oftenGoods" v-on:click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs >
            <el-tab-pane label="汉堡">
              <ul class='cookList'>
                <li v-for="goods in type0Goods"  v-on:click="addOrderList(goods)">
                  <el-row type="flex" class="row-bg" :gutter="10">
                    <el-col :span="8"> <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div></el-col>
                    <el-col :span="12"> <b class="foodName">{{goods.goodsName}}</b><span class="foodPrice">￥{{goods.price}}</span></el-col>
                  </el-row>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <ul class='cookList'>
                <li v-for="goods in type1Goods"  v-on:click="addOrderList(goods)">
                  <el-row type="flex" class="row-bg" :gutter="10">
                    <el-col :span="8"> <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div></el-col>
                    <el-col :span="12"> <b class="foodName">{{goods.goodsName}}</b><span class="foodPrice">￥{{goods.price}}</span></el-col>
                  </el-row>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <ul class='cookList'>
                <li v-for="goods in type2Goods"  v-on:click="addOrderList(goods)">
                  <el-row type="flex" class="row-bg" :gutter="10">
                    <el-col :span="8"> <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div></el-col>
                    <el-col :span="12"> <b class="foodName">{{goods.goodsName}}</b><span class="foodPrice">￥{{goods.price}}</span></el-col>
                  </el-row>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <ul class='cookList'>
                <li v-for="goods in type3Goods">
                  <el-row type="flex" class="row-bg" :gutter="10">
                    <el-col :span="8"> <div class="foodImg"><img :src="goods.goodsImg" width="100%"></div></el-col>
                    <el-col :span="12"> <b class="foodName">{{goods.goodsName}}</b><span class="foodPrice">￥{{goods.price}}</span></el-col>
                  </el-row>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "pos",
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalMoney: 0,
      totalCount: 0,
      num1: 1
    };
  },
  created: function() {
    //服务端拉取 常用oftenGoods
    axios
      .get("http://jspang.com/DemoApi/oftenGoods.php")
      .then(reponse => {
        this.oftenGoods = reponse.data;
      })
      .catch(error => {
        //console.log(error);
        alert("網絡錯誤,無法訪問");
      });

    //服务端拉取 分類typeGoods
    axios
      .get("http://jspang.com/DemoApi/typeGoods.php")
      .then(reponse => {
        this.type0Goods = reponse.data[0];
        this.type1Goods = reponse.data[1];
        this.type2Goods = reponse.data[2];
        this.type3Goods = reponse.data[3];
      })
      .catch(error => {
        //console.log(error);
        alert("網絡錯誤,無法訪問");
      });
  },
  mounted: function() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  methods: {
    addOrderList(goods) {
      //商品是否存在於訂單列表中
      let is_have = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          is_have = true; //存在
        }
      }
      //根據判斷的is_have值編寫
      if (is_have) {
        //改變列表中商品數量
        // let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
        let arr = this.tableData.filter(function(o) {
          return o.goodsId == goods.goodsId;
        });
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      this.getAllMoney();
    },
    //删除单个商品
    delSingleGoods(goods) {
      //返回除了传入的这个goods之外的所有
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    //汇总
    getAllMoney() {
      //汇总量清0
      this.totalMoney = 0;
      this.totalCount = 0;
      if (this.tableData) {
        this.tableData.forEach(element => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + element.price * element.count;
        });
      }
    },
    delAllGoods() {
      this.tableData = [];
      this.totalMoney = 0;
      this.totalCount = 0;
    },
    checkout() {
      if (this.totalCount != 0) {
       //element 自帶信息提示
        this.$message({
          message: "成功结账 ￥"+this.totalMoney+"，坐等收货吧",
          type: "success"
        });
         this.tableData = [];
        this.totalMoney = 0;
        this.totalCount = 0;
      }else{
        this.$message.error('購物車還沒有商品');
     }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title {
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 9px;
  margin: 0;
}
.often-goods-list ul li {
  display: inline-block;
  list-style: none;
  border: 1px solid #e5e9f2;
  padding: 8px 12px;
  margin: 0 10px 5px 0;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  font-weight: bold;
  color: #f56c6c;
}
.buttonGroup {
  margin: 20px 0;
}
.goods-type {
  padding: 0 40px;
}
.cookList {
  padding: 0;
  margin: 0;
}
.cookList li {
  display: inline-block;
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auto;
  overflow: hidden;
  background-color: #fff;
  padding: 4px;
  margin: 0 4px 4px 0;
  cursor: pointer;
}
.foodImg img {
  display: block;
  font-size: 0;
}
.foodName {
  display: block;
  font-size: 16px;
  color: #303133;
}
.foodPrice {
  font-size: 16px;
  color: #f56c6c;
}
</style>



